import React, { useMemo } from "react";
import { StyleSheet } from "react-native";
import { Text as PaperText, TextProps } from "react-native-paper";

function TextNonMemoized<T>(
  props: TextProps<T> & {
    h1?: boolean;
    h2?: boolean;
    h3?: boolean;
    h4?: boolean;
    doNotScaleFonts?: boolean;
  },
) {
  const ariaLevel = props.h1
    ? 1
    : props.h2
    ? 2
    : props.h3
    ? 3
    : props.h4
    ? 4
    : undefined;
  const style = useMemo(
    () => [
      props.h1 && styles.h1,
      props.h2 && styles.h2,
      props.h3 && styles.h3,
      props.h4 && styles.h4,
      props.style,
    ],
    [props.h1, props.h2, props.h3, props.h4, props.style],
  );
  return (
    <PaperText
      style={style}
      maxFontSizeMultiplier={props.doNotScaleFonts === true ? 1.0 : 0}
      accessibilityRole={
        props.h1 || props.h2 || props.h3 || props.h4 ? "header" : undefined
      }
      selectable={false}
      // @ts-ignore We want to send this prop anyway
      ariaLevel={ariaLevel}
      accessibilityLevel={ariaLevel}
      aria-level={ariaLevel}
      {...props}
    />
  );
}

const Text = React.memo(TextNonMemoized);

export { Text };

