import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { Items } from "../../../items/Items";
import { ItemTagEnum } from "../../../items/ItemTagEnum";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { registerRandomEventTrigger } from "../../autorandom/RandomEventTrigger";
import { buildEvent } from "../../EventMessageBuilder";
import { EventTag } from "../../GameEvent";

export const varietyCollectorStoryline = new Storyline(
  "varietyCollector",
  t("Variety Collector"),
  380,
  20,
  t(
    "Reach at least Lv25 on your primary Element and hold at least 3 different Accessories in your inventory. Then, it may happen as a random event",
  ),
  t("Extra Accessory slot"),
  (state) => true,
);

export function loadVarietyCollector() {
  Storylines.register(varietyCollectorStoryline);
  const varietyCollectorEventBuilder = buildEvent(
    "varietyCollector",
    t("(Storyline) Variety Collector"),
    [EventTag.STORYLINE],
  ).setStorylineIds(varietyCollectorStoryline.getId());

  varietyCollectorEventBuilder.message(
    t(
      `Your skill with magic continues to increase! Little by little, you begin getting more confidence in your skills.
      
Focused as you are, however, you are startled when your hired wizard calls for your attention. You glance at her. She is holding her hand up, and you notice the obvious: she is wearing a different ring on each finger!`,
    ),
  );
  varietyCollectorEventBuilder.message(
    t(
      `"Psst", she exclaims. "Accessories are really neat! Why don't you make more of them? I'd love to see a big *collection* of them."
        
You interject. Why collect accessories? You feel it's just clutter. But she continues:

"Come on... I'd really like to try them all out! And besides... I know *something* you don't. What if I tell you what that is - if you make enough of them?"`,
    ),
  );
  varietyCollectorEventBuilder
    .message(
      t(
        `Something you don't know...? You get the feeling she is pulling your leg.
        
Should you really bother?`,
      ),
    )
    .option(
      t(
        "Show her a collection of accessories (Requires 15 different Accessories)",
      ),
      {
        transform: (state) => {
          return state;
        },
        isEnabled: (state: GameState, params) => {
          return (
            state.run.inventory.filter((itemStack) =>
              Items.getById(itemStack.itemOccurrence.itemId)
                .getTags()
                .includes(ItemTagEnum.Accessory),
            ).length >= 15
          );
        },
      },
      "end",
    )
    .option(t("Maybe later"))
    .end();
  varietyCollectorEventBuilder
    .message(
      t(
        `You bring a box up to her and open it. Inside is a large, crowded set of Accessories of all kinds. Your hired wizard looks at it: she appears surprised!
        
You explain to her that you had to leverage knowledge from *every Element* to be able to create such a diverse, varied collection. She better tell you now...`,
      ),
    )

    .tag("end");
  varietyCollectorEventBuilder
    .message(
      t(`"Oh, of course!" She grabs two accessories from the box, and puts them on. You stare at her, confused. Then she simply says:

"You can equip *two* accessories at once!"

...What? All that work... and *that* is what she had to say?

"It's a critical skill! I haven't seen you doing it even once!"`),
    )
    .option(t("Continue"), {
      transform: (state) => varietyCollectorStoryline.complete(state),
    });
  varietyCollectorEventBuilder
    .message(
      t(`You begin explaining your reasoning for it. About the modern style of wizards. How it is entirely out of fashion to do so. She does not seem to be paying attention. Instead, *she grabs your diary - without permission - and writes some scribble on it!*

"I'm doing you a favor! Don't be so thick-skulled!"

You decide to just ignore her...
  
**You have completed the "Variety Collector" Storyline! Unfortunately, you refuse to change your ways... but in future retirements, your hired wizard's scribbles will grant you awareness - you will be able to equip a second accessory, as long as both of them are different!**`),
    )
    .end();

  const varietyCollectorEvent = varietyCollectorEventBuilder.build();

  registerRandomEventTrigger(
    varietyCollectorEvent,
    150,
    (state) =>
      !varietyCollectorEvent.hasTriggered(state) &&
      !varietyCollectorStoryline.isCompleted(state) &&
      state.run.inventory.filter((itemStack) =>
        Items.getById(itemStack.itemOccurrence.itemId)
          .getTags()
          .includes(ItemTagEnum.Accessory),
      ).length >= 3,
  );

  registerTransformation(
    [[TransformationTags.AccessorySize]],
    varietyCollectorStoryline.getId(),
    varietyCollectorStoryline.getName(),
    TransformationType.Addition,
    (state, params) => (varietyCollectorStoryline.isBonusActive(state) ? 1 : 0),
  );
}
