import { StatusBar } from "expo-status-bar";
import React, { useEffect, useRef } from "react";
import {
  ActivityIndicator,
  Animated,
  Easing,
  Platform,
  SafeAreaView,
  StyleSheet,
  View,
} from "react-native";
import { MD3Theme, Provider as PaperProvider } from "react-native-paper";
import {
  SafeAreaProvider,
  initialWindowMetrics,
} from "react-native-safe-area-context";
import { t } from "../utils/I18n";
import { OrientationLockManager } from "../utils/OrientationLockManager";
import { Text } from "./utility/Text";

export function Loading(props: { theme: MD3Theme }) {
  const theme = props.theme;

  const loadingAnim = useRef(new Animated.Value(0.0)).current;

  useEffect(() => {
    Animated.timing(loadingAnim, {
      useNativeDriver: true,
      toValue: 400,
      duration: 60000,
      easing: Easing.out(Easing.poly(5)),
    }).start();
  }, []);

  return (
    <SafeAreaProvider initialMetrics={initialWindowMetrics}>
      <PaperProvider theme={theme}>
        <OrientationLockManager />
        <StatusBar style={theme.dark ? "light" : "dark"} />
        <SafeAreaView
          style={[
            styles.safeAreaContainer,
            {
              backgroundColor: theme.colors.background,
            },
          ]}
        >
          <View style={{ alignItems: "center", width: "100%" }}>
            <ActivityIndicator size="large" />
            {Platform.OS != "web" && (
              <View
                style={{
                  width: 200,
                  marginVertical: 8,
                  borderWidth: 1,
                  borderRadius: 3,
                  height: 6,
                  overflow: "hidden",
                  borderColor: theme.colors.primary,
                }}
              >
                <Animated.View
                  style={{
                    height: 4,
                    width: 1,
                    transform: [{ scaleX: loadingAnim }],
                    backgroundColor: theme.colors.primary,
                  }}
                />
              </View>
            )}
            <Text
              style={{
                color: theme.colors.onBackground,
              }}
            >
              {t("Loading game. Please wait a moment...")}
            </Text>
          </View>
        </SafeAreaView>
      </PaperProvider>
    </SafeAreaProvider>
  );
}

const styles = StyleSheet.create({
  safeAreaContainer: {
    flex: 1,
    justifyContent: "center",
  },
});
